import CustomButton from "components/CustomButton";
import CustomForm from "components/CustomForm";
import useNotification from "hooks/Notifications";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { deleteJobDetails, locumProfileDetails, saveJobHistoryData } from "redux/Actions/Auth";
import { ERROR_MESSAGES, INPUT_TYPES, LABEL, SNACK_VARIANT, STRINGS } from "shared/Constants";
import { Images } from "shared/Images";
import "./styles.scss";

import {
    addJob,
    checkAndDelete,
    checkAndUpdate,
    createDefaultData,
    createEditClick,
    createHistoryDefault,
    createItemData,
    createJobData,
    createJobHistoryData,
    createJobHistoryList,
    createJobListClick,
    createPayloadData,
} from "../../constants";
import CustomJobForm from "./widget/CustomJobHistory";

function JobHistory({ setCvData = () => {} }) {
    const dispatch = useDispatch();
    const profileDetails = useSelector((state) => state.common.locumProfileDetails);
    const cvManage = useSelector((state) => state.common.locumProfileDetails?.cvManage) || {};
    const jobData =
        useSelector((state) => state.common.locumProfileDetails?.cvManage?.job_history) || [];
    const [msg, sendNotifications] = useNotification();
    const [addJobClick, setAddJobClick] = useState(false);
    const [jobHistoryData, setJobHistoryData] = useState([
        {
            type: INPUT_TYPES.TEXT,
            registerName: "company_name0",
            labelName: LABEL.COMPANY_NAME,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.COMPANY_NAME,
                pattern: ERROR_MESSAGES.VALID_COMPANY_NAME,
            },
        },
        {
            type: INPUT_TYPES.TEXT,
            registerName: "job_title0",
            labelName: LABEL.JOB_TITLE,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.JOB_TITLE,
                pattern: ERROR_MESSAGES.VALID_JOB_TITLE,
            },
        },
        {
            type: INPUT_TYPES.DATE,
            registerName: "start_date0",
            labelName: LABEL.REGISTRATION_NO,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DATE,
                pattern: ERROR_MESSAGES.VALID_DATE,
            },
        },
        {
            type: INPUT_TYPES.DATE,
            registerName: "end_date0",
            labelName: LABEL.END_DATE,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DATE,
                pattern: ERROR_MESSAGES.VALID_DATE,
            },
        },
        {
            type: INPUT_TYPES.TEXT,
            registerName: "description0",
            labelName: LABEL.DESCRIPITON,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DESCRIPITON,
                pattern: ERROR_MESSAGES.VALID_DESCRIPTION,
            },
        },
    ]);
    const [jobHistoryList, setJobHistoryList] = useState([
        ...createJobHistoryList(jobData.length, jobData),
    ]);
    const [historyData, setHistoryData] = useState(createDefaultData(jobData));
    const onSubmit = (data, event) => {
        console.log("hitted");
        const payload = createPayloadData(data);
        dispatch(
            saveJobHistoryData(
                { job_history: JSON.stringify(payload) },
                (response) => {
                    setJobHistoryList(createJobListClick(jobHistoryList, true));
                    setHistoryData(createDefaultData(response.data));
                    // setCvData((prev) => {
                    //     return {
                    //         ...prev,
                    //         job_history: [...response.data],
                    //     };
                    // });
                    dispatch(
                        locumProfileDetails({
                            ...profileDetails,
                            cvManage: {
                                ...cvManage,
                                job_history: [...response.data],
                            },
                        }),
                    );
                    if (addJobClick) {
                        setJobHistoryList([
                            ...createJobListClick(jobHistoryList, true),
                            addJob(jobHistoryList.length),
                        ]);
                        setAddJobClick(false);
                    } else {
                        setJobHistoryList(createJobListClick(jobHistoryList, true));
                    }
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const onEditClick = () => {
        setJobHistoryList(createJobListClick(jobHistoryList, false));
    };
    const onDeleteClick = (deleteEvent) => {
        console.log("hitted 2");
        const jobHistoryIndex = deleteEvent.target.id;
        if (historyData[jobHistoryIndex]) {
            dispatch(
                deleteJobDetails(
                    { id: jobData[jobHistoryIndex].id },
                    (response) => {
                        // now we are going to update the local data
                        jobData.splice(jobHistoryIndex, 1);
                        dispatch(
                            locumProfileDetails({
                                ...profileDetails,
                                cvManage: {
                                    ...cvManage,
                                    job_history: [...jobData],
                                },
                            }),
                        );
                        // setCvData((prev) => {
                        //     return {
                        //         ...prev,
                        //         job_history: [...jobData],
                        //     };
                        // });
                        jobHistoryList.splice(jobHistoryIndex, 1);
                        historyData.splice(jobHistoryIndex, 1);
                        setHistoryData([...historyData]);
                        setJobHistoryList(jobHistoryList);
                        sendNotifications({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        } else {
            jobHistoryList.splice(jobHistoryIndex, 1);
            historyData.splice(jobHistoryIndex, 1);
            setHistoryData([...historyData]);
            setJobHistoryList([...jobHistoryList]);
        }
    };
    const handleAddjob = () => {
        setAddJobClick(true);
    };
    console.log(jobData, historyData, "history data");
    return (
        <div className="card mb-4">
            <div className="card-body">
                <div className="title_icon d-flex mb-3">
                    <h6 className="h6 mb-0">{STRINGS.JOB_HISTORY}</h6>
                    <em className="ms-2" onClick={onEditClick}>
                        <img src={Images.Pencil} width="16" />
                    </em>
                </div>
                <CustomJobForm
                    id="jobHistory"
                    formData={jobHistoryList}
                    onSubmit={onSubmit}
                    onDeleteClick={onDeleteClick}
                    defaultValues={createHistoryDefault(historyData)}
                />
                <div className="btn_group text-center">
                    <button
                        type={"submit"}
                        form={`jobHistory`}
                        onClick={handleAddjob}
                        className="btn btn-md btn-outline-secondary"
                    >
                        <svg
                            className="me-2"
                            width="18"
                            height="19"
                            viewBox="0 0 29 28"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <path
                                d="M14.9512 0C13.9337 0 13.1091 0.824671 13.1091 1.84211V12.1579H2.79328C1.77584 12.1579 0.951172 12.9826 0.951172 14C0.951172 15.0174 1.77584 15.8421 2.79328 15.8421H13.1091V26.1579C13.1091 27.1753 13.9337 28 14.9512 28C15.9686 28 16.7933 27.1753 16.7933 26.1579V15.8421H27.1091C28.1265 15.8421 28.9512 15.0174 28.9512 14C28.9512 12.9826 28.1265 12.1579 27.1091 12.1579H16.7933V1.84211C16.7933 0.824671 15.9686 0 14.9512 0Z"
                                fill="#104651"
                            />
                        </svg>{" "}
                        {STRINGS.ADD_JOB}
                    </button>
                    <button type={"submit"} form={`jobHistory`} className="btn btn-md btn-primary">
                        {STRINGS.SAVE}
                    </button>
                </div>
            </div>
        </div>
    );
}

export default JobHistory;
